import Api from '@/common/api';
export default {
	data() {
		return {
			source: 0,
			addressList: [],
		};
	},
	async onLoad(option) {
		this.source = option.source;
		this.getListAddress()
	},
	async onShow(){
		this.getListAddress()
	},
	methods: {
		async getListAddress(){
			let params = {};
			let addressList = await Api.apiCall('get', Api.order.listAddress, params);
			this.addressList = addressList.data;
		},
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				this.$api.prePage().addressId = item.id;
				uni.navigateBack();
			}
		},
		addAddress(type, item) {
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			});
		},
		//添加或修改成功之后回调
		refreshList(data, type) {
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
		},
		// 删除地址
		async delAddress(item){
			console.log(item)
			// let params = {
			// 	id: item.id
			// }
			let data = await Api.apiCall('post', Api.order.deleteAddress, {id: item.id});
			if(data.code==0){
				uni.showToast({
					title: '删除成功',
				})
					this.getListAddress()
			}else{
				uni.showToast({
					title: '删除失败',
					icon: 'none'
				})
			}
			console.log("------删除-------",data)
		},
	}
};
